import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import { InMemoryWebApiModule } from 'angular-in-memory-web-api';
import { TranslateModule } from '@ngx-translate/core';
import 'hammerjs';

import { FuseModule } from '@fuse/fuse.module';
import { FuseSharedModule } from '@fuse/shared.module';
import { CoreModule } from './core/core.module';

import { fuseConfig } from './fuse-config';

import { AppComponent } from './app.component';
import { FuseFakeDbService } from './fuse-fake-db/fuse-fake-db.service';
import { FuseMainModule } from './main/main.module';
import { AppStoreModule } from './store/store.module';
import { AnteraLoginComponent } from './authentication/login/login.component';
import { AnteraLoginModule } from './authentication/login/login.module';
import { AuthGuardService } from './authentication/login/auth-guard.service';

import {
    GoogleApiModule, 
    GoogleApiService, 
    GoogleAuthService, 
    NgGapiClientConfig, 
    NG_GAPI_CONFIG,
    GoogleApiConfig
} from "ng-gapi";

let gapiClientConfig: NgGapiClientConfig = {
    client_id: "721523713281-e6106bjcf42ouu84misma8icvqqu42go.apps.googleusercontent.com",
    discoveryDocs: ["https://analyticsreporting.googleapis.com/$discovery/rest?version=v4"],
    scope: [
        "https://www.googleapis.com/auth/gmail.readonly",
        "https://www.googleapis.com/auth/gmail.send",
        "https://www.googleapis.com/auth/gmail.compose",
        "https://www.googleapis.com/auth/gmail.modify",
        "https://mail.google.com/"
    ].join(" ")
};

const appRoutes: Routes = [

    {
        path        : 'apps',
        canLoad   : [AuthGuardService],
        loadChildren: './main/content/apps/apps.module#FuseAppsModule'
    },
    {
        path        : 'pages',
        canLoad   : [AuthGuardService],
        loadChildren: './main/content/pages/pages.module#FusePagesModule'
    },
    {
        path        : 'ui',
        canLoad   : [AuthGuardService],
        loadChildren: './main/content/ui/ui.module#FuseUIModule'
    },
    {
        path        : 'services',
        canLoad   : [AuthGuardService],
        loadChildren: './main/content/services/services.module#FuseServicesModule'
    },
    {
        path        : 'components',
        canLoad   : [AuthGuardService],
        loadChildren: './main/content/components/components.module#FuseComponentsModule'
    },
    {
        path        : 'components-third-party',
        canLoad   : [AuthGuardService],
        loadChildren: './main/content/components-third-party/components-third-party.module#FuseComponentsThirdPartyModule'
    },
    {
        path        : 'accounts',
        canLoad   : [AuthGuardService],
        loadChildren: './features/accounts/accounts.module#FuseAccountsModule'
    },
    {
        path        : 'contacts',
        canLoad   : [AuthGuardService],
        loadChildren: './features/contacts/contacts.module#FuseContactsModule'
    },
    {
        path        : 'receiving',
        canLoad   : [AuthGuardService],
        loadChildren: './features/receivings/receivings.module#FuseReceivingsModule'
    },
    {
        path        : 'inventory',
        canLoad   : [AuthGuardService],
        loadChildren: './features/inventory/inventory.module#FuseInventoryModule'
    },
    {
        path        : 'webstore',
        loadChildren: './features/webstore/webstore.module#FuseWebstoreModule'
    },
    {
        path        : 'guided-sessions',
        loadChildren: './features/guided-sessions/guided-sessions.module#FuseGuidedSessionsModule'
    },
    {
        path        : 'locations',
        loadChildren: './features/locations/locations.module#FuseLocationsModule'
    },
    {
        path        : 'projects',
        loadChildren: './features/projects/projects.module#FuseProjectsModule'
    },
    {
        path        : 'cases',
        loadChildren: './features/cases/cases.module#FuseCasesModule'
    },
    {
        path      : '**',
        redirectTo: '/apps/dashboards/project'
    }
];

@NgModule({
    declarations: [
        AppComponent
    ],
    imports     : [
        BrowserModule,
        BrowserAnimationsModule,
        HttpClientModule,
        RouterModule.forRoot(appRoutes),
        CoreModule,

        TranslateModule.forRoot(),
        InMemoryWebApiModule.forRoot(FuseFakeDbService, {
            delay             : 0,
            passThruUnknownUrl: true
        }),

        // Fuse Main and Shared modules
        FuseModule.forRoot(fuseConfig),
        FuseSharedModule,
        AnteraLoginModule,

        GoogleApiModule.forRoot({
            provide: NG_GAPI_CONFIG,
            useValue: gapiClientConfig
        }),

        AppStoreModule,
        FuseMainModule
    ],
    bootstrap   : [
        AppComponent
    ]
})
export class AppModule
{
}



