export class User
{
    userId: string;
    username: string;
    firstName: string;
    lastName: string;
    token: string;
    isAdmin: boolean;

    constructor(user)
    {
        this.userId = user.userId;
        this.username = user.username;
        this.firstName = user.firstName;
        this.lastName = user.lastName;
        this.token = user.token;
        this.isAdmin = user.isAdmin;
    }
    
}



