import {Injectable, NgZone} from "@angular/core";
import * as _ from "lodash";
import {GoogleAuthService} from "ng-gapi/lib/GoogleAuthService";
import GoogleUser = gapi.auth2.GoogleUser;
import GoogleAuth = gapi.auth2.GoogleAuth;
import { Router } from "@angular/router";

@Injectable()
export class UserService {
    public static readonly SESSION_STORAGE_KEY: string = "accessToken";
    public static readonly SESSION_USER_ID: string = "userId";

    constructor(private googleAuthService: GoogleAuthService,
                private ngZone: NgZone, private router: Router) {
    }

    public getCurrentUser() {
        return new Promise((resolve, reject) => {
            this.googleAuthService.getAuth().subscribe((auth) => {
                let currUser = auth.currentUser.get();
                resolve(currUser);
            }, (err) => {reject(err)});
        })
    }

    public refreshToken() {
        return new Promise((resolve, reject) => {
            this.googleAuthService.getAuth().subscribe((auth) => {
                let currUser = auth.currentUser.get();
                currUser.reloadAuthResponse().then((resp) => {
                    resolve(resp.access_token);
                }, (err) => {
                    reject(err);
                });
            }, (err) => reject(err));
        })
    }

    public getToken() {
        return new Promise((resolve, reject) => {
            this.googleAuthService.getAuth().subscribe((auth) => {
                let currUser = auth.currentUser.get();
                if (currUser.getAuthResponse().expires_at <= (new Date().getTime() + 300000)) {
                    this.refreshToken().then((resp) => {
                        resolve(currUser.getAuthResponse().access_token);
                    }, (err) => {
                        reject(err);
                    });
                } else {
                    resolve(currUser.getAuthResponse().access_token);
                }
            });
        })
    }

    public signIn() {
        return new Promise((resolve, reject) => {
            this.googleAuthService.getAuth().subscribe((auth) => {
                auth.signIn().then(res => {
                    this.signInSuccessHandler(res).then(_=> {
                        resolve();
                    })
                }, err => reject(this.signInErrorHandler(err)));
            });
        })
    }

    //TODO: Rework
    public signOut(): void {
        this.googleAuthService.getAuth().subscribe((auth) => {
            try {
                auth.signOut();
            } catch (e) {
                console.error(e);
            }
            localStorage.removeItem(UserService.SESSION_STORAGE_KEY)
            localStorage.removeItem(UserService.SESSION_USER_ID);
        });
    }

    //TODO: Rework
    public isUserSignedIn(){
        return new Promise((resolve, reject) => {
            this.googleAuthService.getAuth().subscribe((auth) => {
                resolve(auth.isSignedIn.get());
            },reject);
        });
    }

    private signInSuccessHandler(res: GoogleUser) {
        return new Promise((resolve) => {
            this.ngZone.run(() => {
                localStorage.setItem(
                    UserService.SESSION_STORAGE_KEY, res.getAuthResponse().access_token,    
                );
                localStorage.setItem( UserService.SESSION_USER_ID, res.getId());
                resolve();
            });
        });
    }

    private signInErrorHandler(err) {
        console.warn(err);
        return Promise.reject(err);
    }
}


