import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { MatButtonModule, MatCheckboxModule, MatFormFieldModule, MatInputModule, MatSnackBar, MatSnackBarModule } from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';

import { AnteraLoginComponent } from './login.component';
import { AuthService } from './auth.service';
import { AuthGuardService } from './auth-guard.service';
import { HttpClientModule } from '@angular/common/http';

const routes = [
    {
        path     : 'login',
        component: AnteraLoginComponent
    }
];

@NgModule({
    declarations: [
        AnteraLoginComponent
    ],
    imports     : [
        RouterModule.forChild(routes),

        MatButtonModule,
        MatCheckboxModule,
        MatFormFieldModule,
        MatInputModule,
        HttpClientModule,
        MatSnackBarModule,

        FuseSharedModule
    ],
    providers: [
        AuthService,
        AuthGuardService
    ]
})
export class AnteraLoginModule
{
}



