import { Component, ElementRef, HostBinding, Inject, OnDestroy, Renderer2, ViewEncapsulation } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { Platform } from '@angular/cdk/platform';
import { Subscription } from 'rxjs/Subscription';

import { FuseConfigService } from '@fuse/services/config.service';
import { MailService } from './content/apps/mail/mail.service';

import {Observable} from 'rxjs/Rx';
import { reject } from 'q';

import { navigation } from 'app/navigation/navigation';
import { UserService } from './content/apps/mail/user.service';

@Component({
    selector     : 'fuse-main',
    templateUrl  : './main.component.html',
    styleUrls    : ['./main.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class FuseMainComponent implements OnDestroy
{
    navigation: any;

    onConfigChanged: Subscription;
    timerSubscription: Subscription;

    fuseSettings: any;
    @HostBinding('attr.fuse-layout-mode') layoutMode;

    constructor(
        private _renderer: Renderer2,
        private _elementRef: ElementRef,
        private fuseConfig: FuseConfigService,
        private platform: Platform,
        @Inject(DOCUMENT) private document: any,

        // Used for monitoring mail updates
        private mailService: MailService,
        private userService: UserService
    )
    {
        this.onConfigChanged =
            this.fuseConfig.onConfigChanged
                .subscribe(
                    (newSettings) => {
                        this.fuseSettings = newSettings;
                        this.layoutMode = this.fuseSettings.layout.mode;
                    }
                );

        if ( this.platform.ANDROID || this.platform.IOS )
        {
            this.document.body.className += ' is-mobile';
        }

        this.navigation = navigation;
    }

    ngOnInit()
    {
        // Reset Mail Badge
        this.resetMailBadge();
        // Check latest unread email count after every XX seconds 
        this.userService.isUserSignedIn().then((isLoggedIn) => {
            let isSignedIn = Boolean(isLoggedIn);
            if (isSignedIn == true)
            {
                this.timerSubscription = Observable.interval(10*1000)
                .subscribe(() => 
                    this.mailService.getTotalCount('UNREAD').then((count) => {
                        this.updateMailBadge(count);
                    }).catch((err) => {
                        console.error(err);
                    })
                );
            }
        }) ;          

    }

    ngOnDestroy()
    {
        this.onConfigChanged.unsubscribe();
        if (this.timerSubscription) {
            this.timerSubscription.unsubscribe();
        }    
    }

    addClass(className: string)
    {
        this._renderer.addClass(this._elementRef.nativeElement, className);
    }

    removeClass(className: string)
    {
        this._renderer.removeClass(this._elementRef.nativeElement, className);
    }

    updateMailBadge(badgeCount)
    {
        // Get the mail nav item
        const mailNavItem = this.navigation[6];
        mailNavItem.badge.title = badgeCount;
    }

    resetMailBadge()
    {
        // Get the mail nav item
        const mailNavItem = this.navigation[6];
        mailNavItem.badge.title = 0;
    }
}



